import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default  {
	components: {
		uniPopup,
	},
	data() {
		return {
			show: false,
			type: 'center',
		}
	},
	methods: {
		setPassword(open) {
			if (open === 'tip') {
				this.show = true
			} else {
				this.$refs[open].open()
			}
		},
		cancel(type) {
			if (type === 'tip') {
				this.show = false
				return
			}
			this.$refs[type].close()
		},
		change(e) {
			console.log(e.show)
		},
	},
	onLoad(e){
		
	},
}



